@use "vars" as *;

/* ====================================================
Header
==================================================== */
.main-header{
	position: fixed;
	top: 0;
	left: 0;
	width: 210px;
	height: max(480px,100vh);
	background: #354053;
	&__logo{
		display: block;
		color: #fff;
		font-size: 2.4rem;
		font-weight: bold;
		text-align: center;
		line-height: 50px;
		@include fn-hover;
	}
	&__user{
		padding: 30px 10px;
		color: #fff;
		font-size: 1.4rem;
		text-align: center;
		font-weight: bold;
		border-top: 1px solid rgba(#fff,.6);
		i{
			margin-right: .5em;
			font-size: 130%;
		}
	}
	&__navGlobal{
	}
	&__menu{
		&-item{
			@include font-size(14);
			&.logout .main-header__link{
				background: #2b3443;
				&:hover{
					@media (any-hover: hover) {
						background: #222f44;
					}
				}
			}
			&:first-child a span:before{
				background-image: url(../img/icn-menu01.png);
			}
			&:nth-child(2) a span:before{
				background-image: url(../img/icn-menu02.png);
			}
			&:nth-child(3) a span:before{
				@include ft-awesome(f007);
				opacity: .6;
			}
			&:nth-child(4) a span:before{
				background-image: url(../img/icn-menu03.png);
			}
			&:nth-child(5) a span:before{
				background-image: url(../img/icn-menu07.png);
			}
			&:nth-child(6) a span:before{
				background-image: url(../img/icn-menu04.png);
			}
			&:nth-child(7) a span:before{
				@include ft-awesome(f4ff);
				opacity: .6;
			}
			&:nth-child(8) a span:before{
				@include ft-awesome(f08b);
				opacity: .6;
			}

		}
	}
	&__link{
		display: block;
		margin-bottom: 1px;
		padding: 0 14px;
		color: #fff;
		line-height: 50px;
		font-weight: bold;
		background: #3f4c63;
		transition: .2s;
		span{
			position: relative;
			display: inline-block;
			padding-left: 28px;
			@include p-before(20px,20px){
				left: 0;
				@include v-align;
				font-size: 110%;
				line-height: 20px;
				text-align: center;
				background-position: center;
				background-repeat: no-repeat;
				background-size: 20px auto;
			}
		}
		&:hover{
			@media (any-hover: hover) {
				background: #495872;
			}
		}
		&.is-active{
			background: #00112e;
		}
	}
	
}

