@use "vars" as *;

/* ====================================================
　管理メニューTOP
==================================================== */
.pointAdmin-link{
	$link: &;
	position: relative;
	display: grid;
	@include flex(start,center,wrap);
	gap: 15px 20px;
	margin: 30px 0;
	&-item{
		width: calc(25% - 15px);
		@include mq(xl){
			width: calc(33% - 15px);
		}
		&-btn{
			@include flex(start,center);
			gap: 10px;
			padding: 10px 20px 10px 10px;
			width: 100%;
			color: #fff;
			text-align: left;
			font-family: inherit;
			font-size: 1.6rem;
			font-weight: bold;
			line-height: 1.33;
			background: #3a4651;
			border: 0;
			border-radius: 50px;
			cursor: pointer;
			@media (any-hover: hover) {
				transition: all .2s;
				&:hover{
					background: lighten(#3a4651,10%);
				}
			}
			.small{
				display: block;
				font-size: 80%;
			}
			.icn{
				@include flex(center,center);
				width: 70px;
				height: 70px;
				color: #b0afbc;
				font-size: 180%;
				line-height: 80px;
				text-align: center;
				background: #fff;
				border-radius: 50%;
				img{
					width: 32px;
				}
				+ span{
					display: block;
					width: calc(100% - 80px);
				}
			}
		}
		&.--bl{
			#{$link}-item-btn{
				background: #005abb;
				@media (any-hover: hover) {
					transition: all .2s;
					&:hover{
						background: lighten(#005abb,10%);
					}
				}
			}
		}
		&.--or{
			#{$link}-item-btn{
				background: #eba319;
				@media (any-hover: hover) {
					transition: all .2s;
					&:hover{
						background: lighten(#eba319,10%);
					}
				}
			}
		}
	}
}

/* ====================================================
　ポイント管理
==================================================== */
.file-admin{
	justify-content: center;
	margin-left: auto;
	margin-right: auto;
	max-width: 670px;
	.pointAdmin-link-item{
		min-width: 320px;
	}
}

/* ====================================================
　マスター管理
==================================================== */
.history-row{
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 0 40px;
	&__title{
		margin-bottom: 8px;
	}
}

/* ====================================================
　お申込アイテム
==================================================== */
.table-application{
	th,td{
		&:first-child{
			width: 110px;
		}
		&:nth-child(2),
		&:nth-child(3),
		&:nth-child(5){
			width: 92px;
		}
		&:nth-child(2),
		&:nth-child(5){
			width: 92px;
			text-align: center;
		}
		&:nth-child(3){
			white-space: nowrap;
		}
		&:nth-child(4){
			width: 112px;
			text-align: center;
		}
		&:nth-child(6),
		&:nth-child(8){
			width: 65px;
			text-align: center;
			white-space: nowrap;
		}
		&:nth-child(7){
			width: 210px;
		}
		&:nth-child(9),
		&:nth-child(10){
			width: 130px;
		}
	}
	td{
		&:nth-child(3){
			text-align: right;
		}
	}
}

.table-appicants{
	th,td{
		&:first-child{
			width: 110px;
			text-align: center;
			white-space: nowrap;
		}
		&:nth-child(2){
			width: 160px;
			text-align: center;
			white-space: nowrap;
		}
		&:nth-child(3){
			padding-left: 15px;
			padding-right: 15px;
			width: calc(100% - 270px);
			text-align: left;
		}
	}
}


/* ====================================================
　会員一覧
==================================================== */
.sort-member{
	.sort-block__item{
		&:first-child,
		&:nth-child(3){
			width: calc((100% - 210px) / 2);
		}
		&:nth-child(2){
			width: 150px;
		}
		&:nth-child(4),
		&:nth-child(5){
			width: 195px;
		}
	}
}
.table-member{
	th,td{
		&:first-child{
			width: 124px;
			white-space: nowrap;
		}
		&:nth-child(2){
			width: 96px;
			text-align: center;
		}
		&:nth-child(3){
			width: 130px;
			text-align: center;
		}
		&:nth-child(4){
			width: 120px;
		}
		&:nth-child(5),
		&:nth-child(10){
			width: 80px;
			text-align: center;
		}
		&:nth-child(6){
			width: 85px;
			text-align: center;
		}
		&:nth-child(7),
		&:nth-child(8){
			width: 90px;
		}
		&:nth-child(9){
			width: 100px;
			text-align: center;
		}
	}
}
.select-year{
	@include flex(start,center);
	gap: 10px;
	margin-bottom: 10px;
	font-size: 1.4rem;
	.c-selectBox{
		width: 100px;
	}
}
.point-history-cont{
	.c-scroll-box{
		height: 400px;
	}
}
.point-history{
	&:not(:last-child){
		margin-bottom: 20px;
	}
	&__item{
		padding: 20px;
		background: #f2f2f2;
		border-radius: 10px;
		&:not(:last-child){
			margin-bottom: 10px;
		}
		.point{
			position: relative;
			margin-bottom: 5px;
			padding-left: 25px;
			font-family: $font-en;
			font-size: 1.4rem;
			font-weight: 600;
			@include p-before(20px,20px){
				left: 0;
				@include v-align;
				content: "P";
				color: #fff;
				font-size: 1.3rem;
				text-align: center;
				line-height: 20px;
				background: #354053;
				border-radius: 50%;
			}
			.num{
				font-size: 128.57%;
			}
		}
	}
}

/* ====================================================
　パスワード再設定
==================================================== */
#page-pw_reset03{
	.blk-center__box{
		max-width: 430px;
	}
}

/* ====================================================
　お問い合わせ一覧
==================================================== */
.sort-contact{
	.sort-block__item{
		&:first-child,{
			@include flex(start,end);
			gap: 10px;
			width: 425px;
			font-size:1.4rem;
			.clm{
				width: 195px;
			}
			> span{
				line-height: 40px;
			}
		}
		&:nth-child(2){
			width: 195px;
		}
	}
}
.table-contact{
	th,td{
		&:first-child,
		&:nth-child(4),
		&:nth-child(5){
			white-space: nowrap;
		}
		&:first-child{
			width: 96px;
			text-align: center;
			word-break: break-word;
		}
		&:nth-child(2){
			width: 130px;
			text-align: center;
		}
		&:nth-child(3),
		&:nth-child(5){
			width: 120px;
		}
		&:nth-child(5){
			text-align: center;
		}
		&:nth-child(4){
			width: 80px;
			text-align: center;
		}
		&:nth-child(6){
			width: calc(100% - 450px);
		}
	}
}

/* ====================================================
　バナー管理
==================================================== */
.table-banner{
	th,td{
		&:first-child{
			width: 50px;
			
		}
		&:nth-child(2){
			width: 120px;
			white-space: nowrap;
		}
		&:nth-child(3){
			width: 210px;
			text-align: center;
			img{
				width: 100%;
				max-width: 200px;
				height: auto;
			}
			.url{
				margin-top: 2px;
				font-size: 85%;
				text-align: left;
				word-break: break-all;
			}
		}
		&:nth-child(4),
		&:nth-child(5){
			width: 90px;
			white-space: nowrap;
			text-align: center;
		}
		&:nth-child(6){
			width: 280px;
			word-break: break-all;
			a{
				text-decoration: underline;
			}	
		}
		&:nth-child(7),
		&:nth-child(8){
			width: 120px;
			text-align: center;
			white-space: nowrap;
		}
	}
}

/* ====================================================
　アカウント管理
==================================================== */
.table-account{
	th,td{
		&:first-child{
			width: 60px;
		}
		&:nth-child(3){
			width: 200px;
		}
		&:nth-child(4){
			width: calc(100% - 350px);
			white-space: nowrap;
		}
		&:nth-child(2){
			//width: calc(100% - 450px);
			width: 100px;
		}
	}
}