@use "vars" as *;

/* ====================================================
　LOGIN
==================================================== */
#page-login{
	input[type="email"]{
		&:first-of-type{
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			border-bottom-width: 1px;
			+ input{
				border-top-left-radius: 0;
				border-top-right-radius: 0;
				border-top-width: 1px;
			}
		}
	}
	.c-checkBox input[type=checkbox]:checked+span:before{
		border-radius: 50%;
	}
	
}

.blk-center{
	$blk: &;
	position: relative;
	width: 100vw;
	height: 100vh;
	font-size: 1.4rem;
	text-align: center;
	&__box{
		position: fixed;
		@include v-h-align;
		width: 100%;
		max-width: 360px;
		max-height: 80vh;
	}
	&__title{
		margin-bottom: 60px;
		font-size: 2.4rem;
		font-weight: 500;
	}
	&__txt{
		margin-top: 10px;
		line-height: 1.57;
		a{
			color: $color-gr;
			font-weight: bold;
			@include fn-hover;
		}
	}
	&__btn{
		margin-top: 30px;
	}
	input[type="text"],
	input[type="email"],
	input[type="password"]{
		padding: 8px 20px;
		font-family: $font-en;
		font-size: 1.5rem;
		background: #fff;
		border: 1px solid #ddd;
		border-radius: 4px;
		&::placeholder{
			color: #d7dde4;
		}
	}
	.c-checkBox{
		display: inline-block;
		margin-top: 15px;
	}
	.form-item{
		margin-bottom: 20px;
		text-align: left;
		&__head{
			margin-bottom: 10px;
		}
	}
}

