@use "vars" as *;

/* ====================================================
Control
==================================================== */
.c-fc{ //font-color
	&--ff{
		color: #fff
	}
	&--rd{
		color: $color-rd;
	}
}



.c-ft{//font-family
	&--en{
		font-family: $font-en;
	}
}



.c-fw{//font-weight
	&--lt{
		font-weight: 300!important
	}
	&--rg{
		font-weight: 400!important
	}
	&--md{
		font-weight: 500!important
	}
	&--sbd{
		font-weight: 600!important
	}
	&--bd{
		font-weight: 700!important
	}
	&--bk{
		font-weight: 900!important
	}
}



.c-tx--v {//Vertical writing
	//display: inline-block;
	-ms-writing-mode: tb-rl;
	writing-mode: vertical-rl;
	&-pc{
		@include mq(md-min) {
			-ms-writing-mode: tb-rl;
			writing-mode: vertical-rl;
		}
	}
}



.c-al{//text-align
	&--c{
		text-align: center!important
	}
	&--r{
		text-align: right!important
	}
	&--l{
		text-align: left!important
	}
}



.c-val{//vertical-align
	&--md{
		vertical-align: middle
	}
	&--top{
		vertical-align: top
	}
}





/* ====================================================
Component
==================================================== */
// Common Text style
.c-txt{
	&__01{//14/20
		@include fts(14,20);
	}
	&__02{//15/22
		@include fts(15,22);
	}
}



// Common Title style

.c-ttl{
	&__01{
		padding-top: 10px;
		@include font-size(24);
		border-top: 3px solid #a8afb8;
	}
	&__02{
		margin-bottom: 14px;
		@include font-size(20);
		line-height: 1.2;
	}	
	&__03{
		@include flex(start,center);
		margin-bottom: 14px;
		@include font-size(18);
		line-height: 1.2;
		i{
			margin-right: 8px;
		}
	}	
	&__04{
		position: relative;
		padding-left: 15px;
		@include font-size(15);
		line-height: 1.2;
		@include p-before(4px,100%){
			top: 0;
			left: 3px;
			background: #354053;
			border-radius: 2px;
		}
	}	
}



// Common Button style
%__c-btn{
	position: relative;
	padding: .87em 1.33em;
	width: 100%;
	max-width: 280px;
	color: #fff;
	line-height: 1.2;
	font-family: inherit;
	@include font-size(15);
	letter-spacing: .05em;
	text-align: center;
	background: $color-gr;
	border-radius: 50px;
	border: 0;
	transition: .2s;
	cursor: pointer;
}
%__c-btn--hover{
	&:hover{
		@media (any-hover: hover) {
			background: darken(#47bac1,5%);

		}
	}
}

.c-btn{
	@extend %__c-btn;
	@extend %__c-btn--hover;
	&.--gy{
		background: #b0b0b0;
		&:hover{
			@media (any-hover: hover) {
				background: lighten(#b0b0b0,10%);
			}
		}
	}
	&.--bl{
		background: #005abb;
		&:hover{
			@media (any-hover: hover) {
				background: lighten(#005abb,10%);
			}
		}
	}
	&.--l{
		max-width: 280px;
		font-weight: bold;
	}
	&.--m{
		max-width: 220px;
	}
	&.--s{
		max-width: 160px;
	}
	&.--ss{
		max-width: 130px;
	}
	i{
		margin-right: 10px;
		font-size: 120%;
	}
	&__02{
		min-width: 30px;
		color: #fff;
		font-family: inherit;
		@include font-size(15);
		line-height: 30px;
		text-align: center;
		background: $color-gr;
		border-radius: 4px;
		border: 0;
		transition: .2s;
		cursor: pointer;
		@extend %__c-btn--hover;
	}
}


.c-table{
	&__01{
		width: 100%;
		font-size: 1.4rem;
		background: #fff;
		border-right: 2px solid #ddd;
		border-top: 2px solid #ddd;
		border-radius: 10px;
		border-collapse: separate;
		&-head,
		&-desc{
			padding: 8px 20px;
			line-height: 1.33;
		}
		&-row{
			&:first-child > *:first-child{
				border-radius: 10px 0 0 0;
			}
			&:first-child > *:last-child{
				border-radius: 0 10px 0 0;
			}
			&:last-child > *:first-child{
				border-radius: 0 0 0 10px;
			}
			&:last-child > *:last-child{
				border-radius: 0 0 10px 0;
			}		
		}
		&-head{
			width: 200px;
			text-align: left;
			background: #f2f2f2;
			border-left: 2px solid #ddd;
			border-bottom: 2px solid #ddd;
		}
		&-desc{
			font-size: 1.5rem;
			border-left: 2px solid #ddd;
			border-bottom: 2px solid #ddd;
		}
		.num{
			font-weight: bold;
			font-size: 1.5rem;
			.strong{
				font-family: $font-en;
				font-size: 160%;
			}
		}
	}
	&__02{
		width: 100%;
		background: #fff;
		&-row{
			&:nth-child(odd){
				background: #f5f9fc;
			}
		}
		&-head,
		&-cont{
			padding: 7px 5px;
		}
		&-head{
			color: #fff;
			font-size: 1.3rem;
			font-weight: 400;
			background: #354053;
			&:not(:first-child){
				border-left: 1px solid #fff;
			}
		}
		&-cont{
			font-size: 1.4rem;
			line-height: 1.2;
			border-left: 1px solid #dfdfdf;
			border-bottom: 1px solid #dfdfdf;
			&:last-child{
				border-right: 1px solid #dfdfdf;
			}
		}
		.btn-box{
			@include flex(center,center);
			gap: 4px;
		}
		.label{
			display: inline-block;
			min-width: 80px;
			font-size: 90%;
			text-align: center;
			line-height: 24px;
			border-radius: 15px;
			&.is-accepting{
				color: #fff;
				font-weight: bold;
				background: #0a69d0;
			}
			&.is-test{
				background: rgba($color-gr,.4);
			}
			&.is-end{
				background: #d0d0d0;
			}
		}
		.popup-link{
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 2;
			overflow: hidden;
			height: calc((14px * 1.2) * 2);
			text-decoration: underline;
			cursor: pointer;
			@include fn-hover;
		}
		.script-status{
			margin-top: 5px;
			font-size: 90%;
			line-height: 24px;
			border-radius: 15px;
			background: #ddd;
			&.is-test{
				color: #fff;
				background: #005abb;
			}
			&.is-up{
				color: #fff;
				font-weight: bold;
				background: #eba319;
			}
		}
	}
}

// Scroll Box
.c-scroll-box{
	padding: 15px 20px;
	height: 200px;
	@include fts(14,20);
	background: #fff;
	border: 1px solid #ddd;
	overflow-y: scroll;
	&.is-error{
		border-color: #e84c3d;
		background: #fff7f6;
	}
}




/*-------------------------- 
	form component
-------------------------- */
form{
	@include formReset(); 
	input[type="text"],
	input[type="email"],
	input[type="file"],
	input[type="number"],
	input[type="password"],
	input[type="search"],
	input[type="tel"],
	input[type="url"],
	button{
	}
	textarea{
		resize: none;
		
	}
	.c-checkBox{
		font-size: 1.4rem;
		cursor: pointer;
		input[type="checkbox"],
		input[type="radio"]{
			+ span{
				position: relative;
				display: inline-block;
				padding-left: 25px;
				@include p-before(20px,20px){
					left: 0;
					@include v-align;
					border: 1px solid $color-key;
				}
				@include p-after(20px,20px){
					display: none;
					@include ft-awesome(f00c);
					left: 0;
					@include v-align;
					color: #fff;
					font-size: 1.2rem;
					line-height: 20px;
					text-align: center;
				}
			}
			&:checked{
				+ span{
					&:before{
						background: $color-key;
					}
					&:after{
						display: block;
					}
				}
			}
		}
		input[type="checkbox"]{
			+ span{
				&:before{
					border-radius: 5px;
				}
			}
		}
		input[type="radio"]{
			+ span{
				&:before{
					border: 1px solid $color-key;
					border-radius: 50%;
				}
			}
		}
	}
}
.c-selectBox{
	position: relative;
	width: 100%;
	height: 40px;
	background: #f0f4f8;
	border-radius: 4px;
	@include p-before(12px,8px){
		right: 10px;
		@include v-align;
		line-height: 8px;
		font-size: 90%;
		@include ft-awesome(c-down);
	}
	input{
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		background: none!important;
		z-index: 1;
	}
	select{
		border: none;
		outline: none;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: none!important;
		z-index: 1;
		padding: 0 40px 0 10px;
		line-height: 40px;
		&:active,
		&:focus{
			outline: 0;
		}
	}
	&.--s{
		max-width: 300px;
	}
}


