@use "vars" as *;

/* ====================================================
Layout
==================================================== */
.main-content{
	padding: 50px 20px 50px 280px;
	max-width: 1380px;
	min-width: 1000px;
	@include mq(tb) {
		padding-left: 250px;
	}
	&__head{
		@include flex(between,center);
		margin-bottom: 15px;
		padding-top: 10px;
		border-top: 3px solid #a8afb8;
		.c-ttl__01{
			padding: 0;
			border: 0;
		}
	}
	.btm-block{
		&:not(:last-of-type){
			margin-bottom: 30px;
		}
	}
}

.inner{
	width: 90%;
	margin: 0 auto;
	max-width: 1200px;
	@include mq() {
		width: 100%;
		padding: 0 5%
	}
}

.form-wrap{
	margin-top: 30px;
	padding: 30px;
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 5px;
	.dropzone .dz-preview .dz-image{
		display: block;
	}
}

.form-block{
	&__item{
		@include flex(start,center,wrap);
		@include fts(14,20);
		&:not(:first-child){
			margin-top: 20px;
		}
	}
	&__head{
		width: 200px;
		&.val-t{
			padding-top: .7em;
			align-self: flex-start;
		}
		sup{
			color: $color-rd;
			font-size: 100%;
			font-weight: bold;
		}
	}
	&__cont{
		width: calc(100% - 200px);
		img{
			max-width: 250px;
			height: auto;
		}
		.error{
			margin-top: .5em;
			width: 100%;
			color: $color-rd;
			font-size: 85%;
		}
	}
	&__btn{
		@include flex(center,center,wrap);
		gap: 20px;
		margin-top: 40px;
	}
	input[type="text"],
	textarea{
		padding: 10.5px 10px; 
		background: #f0f4f8;
		border-radius: 4px;
		&.--s{
			max-width: 300px;
		}
		&.--ss{
			max-width: 120px;
		}
	}
	.input-date{
		z-index: 10!important;
	}
	.input-upload{
		position: relative;
		@include flex(start,center,wrap);
		gap: 5px 10px;
		max-width: 350px;
		.icon{
			display: block;
			width: 40px;
			height: 40px;
			line-height: 40px;
			color: #fff;
			font-size: 130%;
			text-align: center;
			background: $color-gr;
			border-radius: 4px;
			&:hover{
				@media (any-hover: hover) {
					cursor: pointer;
					background: darken(#47bac1,5%);
			
				}
			}
		}
		.file-select{
			input[type="file"]{
				display: none;
			}
			
		}
		.file-name{
			padding: 10px;
			width: calc(100% - 50px);
			max-width: 300px;
			background: #f0f4f8;
			border-radius: 4px;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}
	.input-name{
		@include flex(start,center);
		gap: 10px;
		input{
			max-width: 150px;
		}
	}
	.input-birth{
		@include flex(start,center);
		gap: 10px;
		.c-selectBox{
			max-width: 100px;
		}
	}
	.input-numbers{
		&__item{
			@include flex(start,center);
			gap: 10px;
			input{
				max-width: 90px;
				text-align: right;
			}
			span{
				&:first-child{
					width: 4em;
				}
			}
			&:not(:last-child){
				margin-bottom: 10px;
			}
		}		
	}
	.input-address{
		&__item{
			@include flex(start,center);
			gap: 10px;
			&:not(:last-child){
				margin-bottom: 10px;
				input{
					max-width: 150px;
				}
			}
			.c-selectBox{
				max-width: 120px;
			}
		}
	}
	.checkbox-row{
		@include flex(start,center,wrap);
		gap: 10px 20px;
		.c-checkBox:has(.left){
			@include flex(start,center,wrap);
			width: 240px;		
		}
		.left{
			margin-left: 20px;
			@include flex(start,center,wrap);
			gap: 0 10px;
		}
	}
	.period-row{
		@include flex(start,center,wrap);
		gap: 10px;
		.c-selectBox{
			&:first-child,
			&:nth-child(4){
				width: 195px;
			}
			&:last-child,
			&:nth-child(2){
				width: 110px;
			}
		}
	}
	.point-row{
		@include flex(start,center);
		gap: 10px;
		input{
			width: 120px;
		}		
	}
	.message{
		@include fts(14,20);
		text-align: center;
	}
	#js-thum-upload{
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	#js-thum-figure{
		figcaption{
			margin-bottom: 5px;
		}
	}
}
.form-confirm{
	.form-block__head{
		&.val-t{
			padding-top: 0;
		}
	}
}
.sort-block{
	@include flex(between,0,wrap);
	margin: 30px 0 40px;
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 5px;
	&__clmL{
		@include flex(start,start,wrap);
		gap: 20px 30px;
		padding: 20px;
		width: calc(100% - 220px);
	}
	&__clmR{
		@include flex(center,center);
		flex-direction: column;
		gap: 15px;
		padding: 20px;
		width: 220px;
		background: #f2f2f2;
		border-radius: 0 5px 5px 0;
	}
	&__item{
		&-head{
			margin-bottom: 8px;
			font-size: 1.4rem;
		}
	}
	.input-name{
		@include flex(start,center);
		gap: 5px 10px;
		font-size: 1.4rem;
		.clm{
			@include flex(start,center);
			gap: 0 10px;
		}
	}
}
.ft-controls{
	@include flex(center,center,wrap);
	margin-top: 40px;
	gap: 15px 20px;
}

.table-block{
	&__total{
		margin-bottom: 10px;
		font-size: 1.4rem;
	}
	&__scroll{
		overflow-x: scroll;
		@include mq(xl) {
			padding-bottom: 15px;
		}
		table{
			min-width: 900px;
		}
	}
	.table-sticky{
		position: relative;
		overflow-x: hidden;
		overflow-y: auto;
		min-width: 900px;
		max-height: 320px;
		table{
			border-collapse: collapse;
			border-spacing: 0;
			th{
				position: sticky;
				top: 0;
				left: 0;
			}	
		}
	}
}

.result-box{
	margin-bottom: 30px;
	&__title {
		margin-bottom: 8px;
	}
	&__table{
		th,td{
			font-size: 1.4rem;
		}
	}
}

.maeesage{
	@include flex(start,center);
	@include fts(15,22);
	font-weight: bold;
	i{
		margin-right: 8px;
		font-size: 150%;
	}		
	&.is-success{
		margin-bottom: 20px;
		color: $color-gr;
	}
	&.is-error{
		margin-bottom: 30px;
		padding: 10px 15px;
		color: #fff;
		background: #e84c3d;
		border-radius: 5px;
	}
}


.pagenation{
	@include flex(center,center);
	gap: 0 8px;
	margin-top: 30px;
	font-family: $font-en;
	a{
		display: block;
		width: 28px;
		font-size: 1.4rem;
		font-weight: 600;
		line-height: 28px;
		text-align: center;
		border-radius: 50%;
		&.is-active{
			color: #fff;
			font-weight: 500;
			background: #354053;
			transition: .2s;
		}
		@media (any-hover: hover) {
			&:hover:not(.prev,.next){
				color: #fff;
				font-weight: 500;
				background: #354053;
			}
		}
		&.prev:hover,
		&.next:hover{
			@include fn-hover;
		}
	}
}




