@use "vars" as *;

/* ====================================================
Utility
==================================================== */
.pc {
	@include mq() {
		display: none
	}
}
.sp {
	@include mq(md-min){
		display: none
	}
}
.pc-sm {
	@include mq(sm){
		display: none
	}
}
.sp-sm {
	@include mq(sm-min){
		display: none
	}
}

// Hover style
.u-fn-hover{
	display: inline-block;
	@include fn-hover
}

// Position style
.u-pull{
	&--left{
		margin-left: calc(((100vw - 100%) / 2) * -1)
	}
	&--right{
		margin-right: calc(((100vw - 100%) / 2) * -1)
	}
}

// Image Objec Fit
.u-img-of{
	width: 100%;
	height: 100%;
	@include object-fit(cover, center)
}

/**************************\
  Basic Modal Styles
\**************************/
.modal__overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.6);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10!important;
}
.modal__container {
	background-color: #fff;
	width: 100%;
	max-width: 800px;
	max-height: 90vh;
	border-radius: 6px;
	overflow-y: auto;
}
.modal__header {
	padding: 13px 30px;
	background: #eaeaea;
}
.modal__title{
	margin: 0;
}
.modal__subTitle{
	margin-bottom: 10px;
}

.modal__close {
	position: absolute;
	right: 0;
	top: 0;
	width: 50px;
	height: 50px;
	line-height: 50px;
	background: transparent;
	border: 0;
	cursor: pointer;
	&:before {
		font-size: 2rem;
		font-weight: 900;
		content: "\2715";
	}
}
.modal__content {
	padding: 30px;
	.form-block{
		&__head{
			width: 125px;
			&.val-t{
				padding-top: 0;
			}
		}
		&__cont{
			width: calc(100% - 125px);
		}
	}
	.scroll-box{
		overflow-y: scroll;
		height: 200px;
		border-bottom: 1px solid #ddd;
		.c-table__02{
			th{
				position: relative;
				position: sticky;
				top: 0;
			}
			tr:last-child td{
				border-bottom: 0;
			}
		}
	}
}

.modal__footer {
	@include flex(center,center);
	gap: 20px;
	margin-top: 30px;
}

/**************************\
  Demo Animation Style
\**************************/
@keyframes mmfadeIn {
  from {
	opacity: 0;
  }
  to {
	opacity: 1;
  }
}

@keyframes mmfadeOut {
  from {
	opacity: 1;
  }
  to {
	opacity: 0;
  }
}

@keyframes mmslideIn {
  from {
	transform: translateY(15%);
  }
  to {
	transform: translateY(0);
  }
}

@keyframes mmslideOut {
  from {
	transform: translateY(0);
  }
  to {
	transform: translateY(-10%);
  }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}

.dropzone{
	position: relative;
	padding: 0;
	min-height: 1px;
	max-width: 380px;
	background: none;
	border: 0;
	.dz-default.dz-message{
		position: absolute;
		left: 0;
		top: 0;
		margin: 0;
		width: 100%;
		height: 100%;
		.dz-button{
			font-size: 0;
		}
	}
	.dz-preview{
		margin: 0;
		min-height:1px;
		@include flex(start,center);
		flex-direction: row-reverse;
		max-width: 330px;
		min-width: 1px;
		width: auto;
		.dz-details{
			position: relative;
			left: 30px;
			@include flex(start,center);
			padding: 10px;
			background: #f0f4f8;
			border-radius: 4px;
			.dz-size{
				margin: 0;
				width: 70px;
				text-align: left;
				> span{
					display: block;
					padding: 0;
					font-size: 1.3rem;
				}
			}
			.dz-filename{
				//width: 300px;
				width: auto;
				font-size: 1.4rem;
				text-align: left;
				overflow: hidden;
				text-overflow: ellipsis;
				> span{
					border: 0!important;
					border-radius: 0;
					padding: 0!important;
				}
				&:hover span{
					border: 0;
					background: none;
				}
			}
		}
		.dz-image{
			display: none;
			min-width: 250px;
			height: auto;
			background: #f0f4f8;
			img{
				width: 100%;
			}	
		}

		.dz-success-mark,
		.dz-error-mark{
			position: relative;
			top: 0;
			left: 0;
			margin: 0;
			width: 24px;
			height: 24px;
			font-size: 130%;
			animation: auto;
			svg{
				display: none;
			}
		}
		.dz-success-mark{
			@include p-before(20px,20px){
				@include ft-awesome(f058);
				color: #005abb;
				font-size: 2rem;
				line-height: 24px;
			}
		}
		.dz-error-mark{
			@include p-before(20px,20px){
				@include ft-awesome(f057);
				color: $color-rd;
				font-size: 2rem;
				line-height: 24px;
			}
		}
		.dz-error-message{
			top: 30px;
		}
		.dz-progress {
			width: 120px;
			.dz-upload{
				background: $color-gr;
			}
		}
	}
	&.dz-max-files-reached{
		.input-upload{
			display: none;
		}
	}
}

//ポイント履歴切り替え
.js-select-content{
	&:not(:first-child){
		display: none;
	}
}

//アップロード　ローディング画面
.loading{
	display: none;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.6);
	z-index: 10!important;
	&.is-active{
		display: flex;
		animation: appear .5s ease;
	}
	.loader,
	.loader:after {
	  border-radius: 50%;
	  width: 10em;
	  height: 10em;
	}
	.loader {
	  margin: 60px auto;
	  font-size: 10px;
	  position: relative;
	  text-indent: -9999em;
	  border-top: .8em solid rgba(255, 255, 255, 0.2);
	  border-right: .8em solid rgba(255, 255, 255, 0.2);
	  border-bottom: .8em solid rgba(255, 255, 255, 0.2);
	  border-left: .8em solid #ffffff;
	  -webkit-transform: translateZ(0);
	  -ms-transform: translateZ(0);
	  transform: translateZ(0);
	  -webkit-animation: loader 1.1s infinite linear;
	  animation: loader 1.1s infinite linear;
	}
}
@keyframes appear {
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
@-webkit-keyframes loader {
  0% {
	-webkit-transform: rotate(0deg);
	transform: rotate(0deg);
  }
  100% {
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
  }
}
@keyframes loader {
  0% {
	-webkit-transform: rotate(0deg);
	transform: rotate(0deg);
  }
  100% {
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
  }
}

.sortTable{
	.has-sort{
		span{
			@include flex(center,center);
			position: relative;
			cursor: pointer;
			&:after { 
				content: '▲';
				font-size: 12px;
				margin-left: 5px;
			}
		}
		&.tablesorter-headerAsc{
			span:after{
			content: "▲";
			}	
		}
		&.tablesorter-headerDesc{
			span:after{
				content: "▼";
			}	
		}
	}
}
